iframe {
  display: none;
}

body {
  background-color: #09142c;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

#App__title {
  display: block;
  margin: 30px;
  color: #fff;
  text-align: center;
}

::-webkit-scrollbar-track {
  background-color: #c0c1c2;
}

::-webkit-scrollbar {
  width: 5px;
  background-color: #c0c1c2;
}

::-webkit-scrollbar-thumb {
  background-color: #808080;
}
